import React from "react";

import { formatTimesmap } from "../../components/Utils";

const InfoList = ({ bot, nick, type, status, created, lastLogIn }) => {
    let statusText;
    switch(status) {
      case "noToken": 
        statusText = <span className="label accent"> BOT </span>;
        break;
      case "noUsername": 
        statusText = <span className="label accent">  botUsername</span>;
        break;
      case "APIError": 
        statusText = <span className="label danger">API Telegram </span>;
        break;
      case "noWebhook": 
        statusText = <span className="label warning">WebHook  </span>;
        break;
      case "service": 
        statusText = <span className="label warning"> </span>;
        break;
      case "DBError": 
        statusText = <span className="label danger">  </span>;
        break;
      case "invalidToken": 
        statusText = <span className="label danger"> BOT </span>;
        break;
      case "ok": 
        statusText = <span className="label okay">OK</span>;
        break;
      default: 
        statusText = <span className="label warning">{status}</span>;
    }
    return(
      <div className="row">
        <div className="col-2">
          <ul>
            <li><b>:</b> <a href={`https://t.me/${nick}`} target="_blank">{bot}</a></li>
            <li><b>:</b> {nick}</li>
            <li><b>:</b> {type}</li>
          </ul>
        </div>
        <div className="col-2">
          <ul>
            <li><b>:</b> {statusText}</li>
            <li><b>:</b> { created && formatTimesmap(created) }</li>
            <li><b>  :</b> { lastLogIn && formatTimesmap(lastLogIn) }</li>
          </ul>
        </div>
      </div>
    );
  }

export default InfoList;
