import React, { useState } from 'react';

import { NavLink } from 'react-router-dom';

import styles from './Navigation.module.less';

export const NavigationTop = () => {
  const [mobileMenu, setmobileMenu] = useState(false);
    return(
    <nav className={styles.mainNav}>
      <div className={styles.mabileMainNav} onClick={() => setmobileMenu(!mobileMenu)}> <i className={`petalicon ${mobileMenu ? 'petalicon-cross': 'petalicon-list'}`}></i> {mobileMenu && '' || '' }</div>
			<ul className={mobileMenu ? styles.show: ''}>
				<li><NavLink to="/dashboard" className={({ isActive }) => isActive ? styles.active : ""}></NavLink></li>
				<li><NavLink to="/categories" className={({ isActive }) => isActive ? styles.active : ""}></NavLink></li>
        <li><NavLink to="/goods" className={({ isActive }) => isActive ? styles.active : ""}></NavLink></li>
        <li><NavLink to="/purchases" className={({ isActive }) => isActive ? styles.active : ""}></NavLink></li>
        <li><NavLink to="/settings" className={({ isActive }) => isActive ? styles.active : ""}></NavLink></li>
			</ul>
		</nav>
    );
}

export const NavigationLeft = () => {
  return (
      <nav className={styles.sideNav}>
      <h5 className={styles.sideNavHeader}></h5>
      <ul>
        <li><NavLink to="/dashboard" className={({ isActive }) => isActive ? styles.active : ""}><i className="petalicon petalicon-home"></i> </NavLink></li>
        <li><NavLink to="/categories" className={({ isActive }) => isActive ? styles.active : ""}><i className="petalicon petalicon-list"></i> </NavLink></li>
        <li><NavLink to="/goods" className={({ isActive }) => isActive ? styles.active : ""}><i className="petalicon petalicon-tile"></i> </NavLink></li>
        <li><NavLink to="/purchases" className={({ isActive }) => isActive ? styles.active : ""}><i className="petalicon petalicon-cart"></i> </NavLink></li>
        <li><NavLink to="/settings" className={({ isActive }) => isActive ? styles.active : ""}><i className="petalicon petalicon-tools"></i> </NavLink></li>
      </ul>
      </nav>
  );
};