import React from "react";

const CrystalPay = ({ info }) => {
  return (
      <ul>
        <li>ID: {info?.id}</li>
        <li>: {info?.error ? info?.error : ''}</li>
        <li>: {(info?.paymethod !== null) ? info?.paymethod : ''}</li>
      </ul>
  );
}

export default CrystalPay;
